<template>

    <div data-v-2adee404="" class="page">
        <div data-v-152ab910="" data-v-2adee404="" class="sub-header">
            <div data-v-152ab910="" class="back" @click="$route.query.from==='pcenter'?$router.push('/pcenter'):$router.go(-1)"></div>
            <div data-v-152ab910="" class="title">选择收货地址</div><!----></div>
        <div data-v-2adee404="" class="main" v-show="addressList.length>0">
            <div data-v-2adee404="" class="address-nav">
                <div data-v-2adee404="" class="address-nav-name-1">配送地址</div>
                <div data-v-2adee404="" class="address-nav-name-2" @click="$router.push('/address/add')">+添加收货地址</div>
            </div>
            <div data-v-2adee404="" class="address-list" @click.stop="selectAddress(item.aid)" v-for="(item,index) in addressList" :key="index">
                <div data-v-2adee404="" class="address-info-wrap">
                    <div data-v-2adee404="" class="check-mark"  v-if="item.isdefault==='1'?true:false"></div>
                    <div data-v-2adee404="" class="address-info default">
                        <div data-v-2adee404="" class="person"><span data-v-2adee404="">{{item.name}}</span><span
                                data-v-2adee404="">{{item.cellphone}}</span></div>
                        <div data-v-2adee404="" class="address"><span data-v-2adee404="" v-if="item.isdefault==='1'?true:false" class="default">默认</span><span
                                data-v-2adee404="" class="text">{{item.province}} {{item.city}} {{item.area}}</span></div>
                    </div>
                </div>
                <div data-v-2adee404="" class="handle-wrap">
                    <div data-v-2adee404="" class="edit" @click.stop="$router.push('/address/mod?aid='+item.aid)"></div>
                    <div data-v-2adee404="" class="del" @click.stop="delAddress(index,item.aid)"></div>
                </div>
            </div>

            <div data-v-2adee404="" v-show="addressList.length<=0" class="no-data">您还没有添加收货地址！</div>
        </div>
    </div>
</template>

<script>
    import  {mapActions,mapState} from  "vuex"
    import Vue from 'vue';
    import { Dialog } from 'vant';
    // 全局注册
    Vue.use(Dialog);
    export default {
        created(){
            //安全认证
            this.$utils.safeUser(this);
            //收货地址列表
            this.getAddressList()
        },
        mounted(){
            document.title = this.$route.meta.title
        },
        computed:{
            ...mapState({
                //收货地址列表
                addressList:state=>state.address.addressList
            })
        },
        methods:{
            ...mapActions({
                //收货地址列表
                getAddressList:"address/getAddressList",
                //删除收货地址
                //删除收货地址
                getDeladdressList:"address/getDeladdressList"
            }),
            //删除收货地址
             delAddress(index,aid){
                Dialog.confirm({
                    title: '',
                    message: '确认要删除吗?',
                })
                    .then(() => {
                        this.getDeladdressList({index:index,aid:aid});
                    })
                    .catch(() => {
                        // on cancel
                    });


            },
            //选择收货地址
            selectAddress(aid){
                //临时存储aid
                sessionStorage['aid']=aid;

                    this.$router.go(-1);


            }
        }
    }
</script>

<style scoped>
    .page {
        width: 100%;
        height: 100vh;
        overflow: hidden;
        background-color: #FFFFFF;
    }
    .sub-header {
        width: 100%;
        height: 1rem;
        background-color: #FFFFFF;
        display: flex;
        display: -webkit-flex;
        align-items: center;
        -webkit-align-items: center;
        border-bottom: 1px solid #EFEFEF;
        position: fixed;
        z-index: 10;
        left: 0;
        top: 0;
        justify-content: space-between;
        padding: 0px 0.1rem;
        box-sizing: border-box
    }

    .sub-header .back {
        width: 0.8rem;
        height: 0.8rem;
        background-image: url("../../assets/images/home/goods/back.png");
        background-size: 100%;
        background-repeat: no-repeat;
        background-position: center;
    }

    .sub-header .title {
        width: 79%;
        height: auto;
        font-size: 0.32rem;
        text-align: center;
        position: absolute;
        z-index: 1;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%)
    }

    .sub-header .right-btn {
        width: auto;
        height: auto;
        font-size: 0.32rem;
    }


    .main {
        width: 100%;
        margin-top: 1rem;
    }

    .main .address-nav {
        width: 100%;
        height: 0.8rem;
        background-color: #D3C6BE;
        font-size: 0.32rem;
        display: flex;
        display: -webkit-flex;
        justify-content: space-between;
        -webkit-justify-content: space-between;
        align-items: center;
        -webkit-align-items: center;
    }

    .main .address-nav .address-nav-name-1 {
        margin-left: 0.2rem;
    }

    .main .address-nav .address-nav-name-2 {
        margin-right: 0.2rem;
    }

    .main .address-list {
        width: 100%;
        height: 2.22rem;
        border-bottom: 1px solid #C6C6C6;
    }

    .main .address-list .address-info-wrap {
        width: 90%;
        height: 1.42rem;
        border-bottom: #C6C6C6 1px dashed;
        margin: 0 auto;
        display: flex;
        display: -webkit-flex;
        align-items: center;
        -webkit-align-items: center;
    }

    .main .address-list .address-info-wrap .check-mark {
        width: 0.4rem;
        height: 0.4rem;
        background: url("../../assets/images/home/cart/gou.png") no-repeat center;
        background-size: 100%;

        margin-right: 0.3rem;
    }

    .main .address-list .address-info-wrap .address-info.default {
        width: 90%;
    }

    .main .address-list .address-info-wrap .address-info {
        width: 100%;
        height: auto
    }

    .main .address-list .person {
        width: 100%;
        height: 0.5rem;
        border-bottom: #EFEFEF 1px solid;
        font-size: 0.28rem;
        overflow: hidden;
    }

    .main .address-list .person span:nth-child(1) {
        margin-right: 0.2rem;
    }

    .main .address-list .address {
        width: 100%;
        height: 0.6rem;
        overflow: hidden;
        line-height: 0.6rem;
        margin-top: 0.1rem;
        white-space: nowrap;
        text-overflow: ellipsis;
    }

    .main .address-list .address .text {
        font-size: 0.32rem;
    }

    .main .address-list .address .default {
        padding: 0.05rem 0.1rem;
        background-color: #FDB709;
        color: #FFFFFF;
        font-size: 0.24rem;
        border-radius: 4px;
        margin-right: 0.2rem;
    }

    .main .address-list .handle-wrap {
        width: 100%;
        height: 0.76rem;
        display: flex;
        display: -webkit-flex;
        justify-content: flex-end;
        -webkit-justify-content: flex-end;
        align-items: center;
        align-items: center;
    }

    .main .address-list .handle-wrap .edit {
        width: 0.4rem;
        height: 0.4rem;
        background: url("../../assets/images/home/cart/edit.png") no-repeat;
        background-size: 100%;

        margin-right: 0.3rem;
    }

    .main .address-list .handle-wrap .del {
        width: 0.4rem;
        height: 0.4rem;
        background: url("../../assets/images/home/cart/del.png")no-repeat center;
        background-size: 100%;

        margin-right: 0.3rem;
    }
</style>